import React from "react";
import { ReactComponent as HillImg } from "./assets/art/svg/hill.svg";
import { makeStyles } from "@material-ui/core/styles";
export default function Hill(props) {
  const useStyles = makeStyles((theme) => ({
    hill: {
      marigin: "0 auto",
      height: (756 * props.height) / 1080,
      width: (1980 * props.height) / 1080
    },
  }));
  const classes = useStyles();
  return (
    <div className={classes.hill}>
      <HillImg />
    </div>
  );
}
